.landingPage_container {
    width:85%;
    margin: 3rem auto;
}
.landingPage_title {
    margin: 2rem 0;
    text-align: center;
}
.landing_video_thumbnail {
    position: relative;
}
.landing_video_duration {
    font-size: 12px;
    line-height: 20px;
    opacity: .8;
    padding: 0 .8rem;
    letter-spacing: 1px;
    background-color: rgba(17, 17, 17, .8);
    position: absolute;
    bottom: 0;
    right: 0;
    color: aliceblue;
}
.landing_video_thumbnail img {
    width: 100%;
}
.landing_video_info_container {
    margin: .5rem 0;
    display: flex;  
}
.landing_video_writer_thumbnail img {
    width: 70%;
}
.landing_video_info {
    display: flex;
    flex-direction: column;
}